// src/pages/Login.jsx
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  TextField,
  Button,
  Typography,
  Container,
  Paper,
  Alert,
  Snackbar,
} from '@mui/material';

const Login = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    username: '',
    password: '',
  });
  const [errors, setErrors] = useState({});
  const [showError, setShowError] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');

  // 模拟用户数据
  const mockUsers = [
    { username: 'admin123', password: 'admin123456' },
    { username: 'test_user', password: 'test123456' },
    { username: 'demo_user', password: 'demo123456' },
  ];

  // 表单验证规则
  const validationRules = {
    username: /^[a-zA-Z0-9_]{4,16}$/,
    password: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
  };

  const validateField = (name, value) => {
    if (!validationRules[name].test(value)) {
      setErrors(prev => ({
        ...prev,
        [name]: name === 'username' 
          ? '用户名必须是4-16位字母、数字或下划线' 
          : '密码必须至少8位，包含字母和数字'
      }));
      return false;
    }
    setErrors(prev => ({ ...prev, [name]: '' }));
    return true;
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    validateField(name, value);
  };

  // 模拟登录验证
  const validateLogin = (username, password) => {
    const user = mockUsers.find(user => user.username === username);
    if (!user) {
      return { success: false, message: '用户名不存在' };
    }
    if (user.password !== password) {
      return { success: false, message: '密码错误' };
    }
    return { success: true, message: '登录成功' };
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const isUsernameValid = validateField('username', formData.username);
    const isPasswordValid = validateField('password', formData.password);

    if (isUsernameValid && isPasswordValid) {
      const loginResult = validateLogin(formData.username, formData.password);
      
      if (loginResult.success) {
        // 存储登录状态
        localStorage.setItem('isLoggedIn', 'true');
        localStorage.setItem('username', formData.username);
        navigate('/home');
      } else {
        setErrorMessage(loginResult.message);
        setShowError(true);
      }
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Box
        sx={{
          marginTop: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        <Paper elevation={3} sx={{ p: 4, width: '100%' }}>
          <Typography component="h1" variant="h5" align="center" gutterBottom>
            登录
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="username"
              label="用户名"
              name="username"
              autoComplete="username"
              autoFocus
              value={formData.username}
              onChange={handleChange}
              error={!!errors.username}
              helperText={errors.username}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="密码"
              type="password"
              id="password"
              autoComplete="current-password"
              value={formData.password}
              onChange={handleChange}
              error={!!errors.password}
              helperText={errors.password}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              登录
            </Button>
            
            {/* 用于测试的账号提示 */}
            <Typography variant="body2" color="text.secondary" align="center">
              测试账号：test_user<br />
              测试密码：test123456
            </Typography>
          </Box>
        </Paper>
      </Box>

      {/* 错误提示 */}
      <Snackbar
        open={showError}
        autoHideDuration={3000}
        onClose={() => setShowError(false)}
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
      >
        <Alert 
          onClose={() => setShowError(false)} 
          severity="error" 
          sx={{ width: '100%' }}
        >
          {errorMessage}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default Login;